import React, { useEffect } from 'react';
import { useDispatch, useSelector } from 'react-redux';
import { Link } from 'react-router-dom';
import { addToCart, removeFromCart } from '../actions/cartActions';

function CartScreen(props){

    const state = useSelector(state => state);
    console.log(state);

    const userSignin = useSelector(state=>state.userSignin);
    const {userInfo} = userSignin;

    const cart = useSelector(state => state.cart);
    const {cartItems} = cart;

    const previous = useSelector(state => state.previous);
    const {previousItems} = previous;

    const productId = props.match.params.id;
    const qty = props.location.search ? Number(props.location.search.split("=")[1]):1;

    const dispatch = useDispatch();

    const removeFromCartHandler = (productId) => {
        dispatch(removeFromCart(productId));
    }

    useEffect(() =>{
        if(productId){
            dispatch(addToCart(productId, qty));
        }
    }, [])

    const checkoutHandler = () =>{
        props.history.push("/signin?redirect=shipping");
    }

    return <div className="cart-page">

    <div className="cart">
        <div className="cart-sidebar"></div>
        <div className="cart-list">
            <ul className="cart-list-container">
                <li>
                    <h3>Shopping Cart</h3>
                </li>
                {
                    cartItems.length === 0 ?
                    <div className="empty-cart">
                        <h2>Your Cart is empty</h2>
                        {
                            userInfo ? 
                            <Link to="/">Discover Products</Link>
                            :
                            <span>
                                <Link>Sign Up</Link>  |  <Link>Sign In</Link>
                            </span>
                        }
                        
                    </div>
                     :
                    cartItems.map( item =>
                        <li key={item.product}>
                            <div className="cart-image">
                                <img src={item.image} alt="product" />
                            </div>
                            <div className="cart-name">
                                <div><Link className="cart-link" to={"/product/"+item.product}> {item.name} </Link></div>
                                <div>
                                    Qty:
                                    <select value={item.qty} onChange={(e)=> dispatch(addToCart(item.product, e.target.value))}>
                                    {[...Array(item.countInStock).keys()].map(x=>
                                        <option key={x+1} value={x+1}>{x+1}</option>
                                    )}
                                    </select>
                                    
                                </div>
                                <button type="button" className="button" onClick={() => removeFromCartHandler(item.product)}>Remove</button>
                            </div>
                            <div className="cart-price">
                                ${item.price}
                            </div>
                        </li>
                    )
                }
            </ul>
        </div>
        <div className="cart-action">
                {
                    cartItems.length === 0 &&
                    <h2>What You've Looked At</h2>
                }
                {
                    cartItems.length === 0 ?
                    previousItems.map( item =>
                        <li key={item.product}>
                            <div className="previous-item-image">
                                <img src={item.image} alt="product" />
                            </div>
                            <div className="previous-item-name">
                                <div><Link className="previous-item-link" to={"/product/"+item.product}> {item.name} </Link></div>
                                <div className="previous-item-rating">
                                    {item.rating} Stars | {item.numReviews}                                    
                                </div>
                                <div className="previous-item-price">
                                ${item.price}
                                </div>
                            </div>
                            
                        </li>
                    )

                     :
                     <span>
                        <h3>
                            Subtotal ({cartItems.reduce((a, c) => a + c.qty, 0)} items)
                            :<br />
                            ${cartItems.reduce((a, c) => a + c.price * c.qty, 0)}
                        </h3>
                        <button className="button primary" disabled={cartItems.length===0} onClick={checkoutHandler}>Checkout</button>
                     </span>
                }

        </div>
    </div>
    </div>

}

export default CartScreen;